<template>
  <div class="posts">
    <b-container fluid="xl">
      <b-row>
        <b-col
          cols="12"
          v-for="post in userPosts"
          :key="post.id"
          class="post p-0 py-1"
        >
          <PostItem :post="post" />
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import PostItem from "./PostItem.vue";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("posts");
export default {
  name: "Posts",
  components: {
    PostItem,
  },
  computed: {
    ...mapState(["userPosts"]),
  },
  methods: {
    ...mapActions(["fetchUserPosts"]),
  },
  created() {
    this.fetchUserPosts(this.$route.params.id);
  },
};
</script>
<style lang="scss" scoped>
.posts {
  max-width: 600px;
  margin: 0 auto;
}
.post:not(:last-child) {
  margin-bottom: 10px;
}
</style>
